
.wrapper {
  padding: 15px;
  background: rgba(255, 255, 255, 0.192);
  backdrop-filter: blur(10px);
  min-height: 70vh;
  width: 90%;
  border-radius: 15px;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  @media (max-width: 800px) {
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
  }
}

.box {
  background-color: blue;
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.icon {
  font-size: 20px;
  font-weight: 600;
  color: rgb(67, 7, 67);
  width: 50px;
  height: 50px;
  border: 3px solid  rgb(93, 9, 93, 1);
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
  position: absolute;
  border-radius: 50%;
  cursor: pointer;
  transition: all 0.5s ease, opacity 2s;
  z-index: 100;
}

.icon:hover {
  width: 150px;
  height: 150px;
  font-size: 30px;
  border: 1px solid  rgb(93, 9, 93, 0);
  text-shadow: 5px 5px 3px rgba(163, 53, 163, 0.8);
}

.iconClicked {
  animation: clickedIcon 1s linear;
  opacity: 0;
}


.circle {
  width: 150px;
  height: 150px;
  border-radius: 50%;
  position: absolute;
  border-bottom: 3px solid rgb(93, 9, 93, 0);
  transition: border .3s ease, opacity 1s;
}

.circleClicked {
  animation: clickedCircle 2s linear;
  opacity: 0;
}

.circle1 {
  margin: auto;
  margin-top: 15px;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 120px;
  height: 120px;
  border-radius: 50%;
  border-top: 3px solid rgb(93, 9, 93, 0);
  transition: border .3s ease, opacity 2s;
}

.circle1Clicked1 {
  animation: clickedCircle1 2s linear;
  opacity: 0;
}

.circle2 {
  margin: auto;
  width: 90px;
  height: 90px;
  border-radius: 50%;
  border-left: 3px solid rgb(93, 9, 93, 0);
  transition: border .3s ease, opacity 2s;
}

.circleClicked2 {
  animation: clickedCircle2 2s linear;
  opacity: 0;
}

.circle3 { 
  margin: auto;
  margin-top: 15px;
  width: 60px;
  height: 60px;
  border-radius: 50%;
  border-right: 3px solid rgb(93, 9, 93, 0);
  text-align: center;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: border .3s ease, opacity 1s;
}     

.circleClicked3 {
  animation: clickedCircle3 2s linear;
  opacity: 0;
}

.rotateC1 {
  animation: rotation1 1.5s infinite linear;
  border-top: 3px solid rgb(93, 9, 93, 1);
}

.rotateRight {
  animation: rotation0 1.5s infinite linear;
  border-bottom: 3px solid rgb(93, 9, 93, 1);
}

.rotateC2 {
  animation: rotation2 1.5s infinite linear;
  border-left: 3px solid rgb(93, 9, 93, 1);
}

.rotateC3 {
  animation: rotation3 1.5s infinite linear;
  border-right: 3px solid rgb(93, 9, 93, 1);
}

@keyframes clickedCircle {
  from {
    width: 150px;
    height: 150px;
    border-top: 3px solid rgb(93, 9, 93, 1);
  }
  to {
    width: 220px;
    height: 220px;
    border-bottom: 3px solid rgb(93, 9, 93, 0);
  } 
}

@keyframes clickedCircle1 {
  from {
    width: 120px;
    height: 120px;
    border-top: 3px solid rgb(93, 9, 93, 1);
  }
  to {
    width: 190px;
    height: 190px;
    border-bottom: 3px solid rgb(93, 9, 93, 0);
  } 
}

@keyframes clickedCircle2 {
  from {
    width: 90px;
    height: 90px;
    border-top: 3px solid rgb(93, 9, 93, 1);
  }
  to {
    width: 160px;
    height: 160px;
    border-bottom: 3px solid rgb(93, 9, 93, 0);
  } 
}

@keyframes clickedCircle3 {
  from {
    width: 60px;
    height: 60px;
    border-top: 3px solid rgb(93, 9, 93, 1);
  }
  to {
    width: 130px;
    height: 130px;
    border-bottom: 3px solid rgb(93, 9, 93, 0);
  } 
}
 

@keyframes clickedIcon {
  from {
    font-size: 30px;
    text-shadow: 5px 5px 3px rgba(163, 53, 163, 0.8);
    opacity: 1;
  }

  to {
    font-size: 100px;
    text-shadow: 20px 20px 15px rgba(163, 53, 163, 0);
    opacity: 0;
  }
}

@keyframes rotation0 {
  from {
    transform:rotate(0deg);
    -ms-transform:rotate(0deg);
    -webkit-transform:rotate(0deg);
  }

  to {
    transform:rotate(90deg);
    -ms-transform:rotate(360deg);
    -webkit-transform:rotate(360deg);
  }
}

@keyframes rotation1 {
  from {
    transform:rotate(0deg);
    -ms-transform:rotate(0deg);
    -webkit-transform:rotate(0deg);
  }

  to {
    transform:rotate(-180deg);
    -ms-transform:rotate(-720deg);
    -webkit-transform:rotate(-720deg);
  }
}

@keyframes rotation2 {
  from {
    transform:rotate(0deg);
    -ms-transform:rotate(0deg);
    -webkit-transform:rotate(0deg);
  }

  to {
    transform:rotate(270deg);
    -ms-transform:rotate(1080deg);
    -webkit-transform:rotate(1080deg);
  }
}

@keyframes rotation3 {
  from {
    transform:rotate(0deg);
    -ms-transform:rotate(0deg);
    -webkit-transform:rotate(0deg);
  }

  to {
    transform:rotate(-360deg);
    -ms-transform:rotate(-1440deg);
    -webkit-transform:rotate(-1440deg);
  }
}