.App {
  margin: 0;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  background: linear-gradient(0deg, rgba(34,193,195,1) 0%, rgba(253,187,45,1) 100%);
}

.navBar {
  margin-top: 20px;
  list-style: none;
  color: gold;
  display: flex;
  font-size: 20px;
}

.navBar a {
  margin-right: 10px;
  list-style: none;
  text-decoration: none;
  color: gold;
}

.navBar a:hover {
  color: orangered;
  text-shadow: darkorange;
}

