.button {
  margin-top: 10px;
  border: none;
  width: 110px;
  height: 40px;
  border-radius: 20px;
  font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
  font-size: 20px;
  font-weight: bold;
  color: #094909;
  background-color: aliceblue;
  transition: all 0.3s ease-in-out;
}

.button:hover {
  width: 115px;
  height: 42px;
  background-color: #094909;
  color: aliceblue;
}