.wrapper {
  margin-top: 50px;
  padding: 15px;
  text-align: center;
  width: 500px;
  height: 300px;
  color: white;
  background-color: rgb(230, 1, 93, 0.7);
  border-radius: 20px;
  box-shadow: 2px 2px 10px gray;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
  font-size: medium;
}

.addBtn {
  position: relative;
  left: 44%;
  top: -1%;
  font-size: 40px;
  background-color: wheat;
  width: 40px;
  height: 40px;
  border-radius: 50%;
  color: black;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  transition: all 0.3s ease-in-out;
}

.addBtn p {
  margin-top: 29px;
}

.addBtn:hover {
  background-color: black;
  color: antiquewhite;
}